'use client';

import { useState } from 'react';
import { useRouter, usePathname, useSearchParams } from 'next/navigation';

type SortOption = {
  label: string;
  value: string;
  direction: 'asc' | 'desc';
};

export const sortOptions: Record<string, SortOption> = {
  title: { label: 'Title', value: 'title', direction: 'asc' },
  year: { label: 'Year', value: 'year', direction: 'desc' },
  duration: { label: 'Length', value: 'duration', direction: 'desc' },
  rating: { label: 'Rating', value: 'rating', direction: 'desc' },
};

export default function SortOptions() {
  const router = useRouter();
  const pathname = usePathname();
  const searchParams = useSearchParams();
  
  const currentSort = searchParams.get('sort') || 'title';
  
  const handleSortChange = (sortKey: string) => {
    const params = new URLSearchParams(searchParams);
    params.set('sort', sortKey);
    router.push(`${pathname}?${params.toString()}`);
  };

  return (
    <div className="relative mb-8">
      <div className="flex items-center justify-between bg-black/10 dark:bg-white/5 backdrop-blur-lg rounded-xl p-1 overflow-hidden">
        {Object.entries(sortOptions).map(([key, option]) => (
          <button
            key={key}
            onClick={() => handleSortChange(key)}
            className={`relative px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
              currentSort === key 
                ? 'text-black dark:text-white bg-white dark:bg-black/50' 
                : 'text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-200'
            }`}
          >
            {option.label}
            <span className="ml-1 text-xs opacity-60">
              {option.direction === 'asc' ? '↑' : '↓'}
            </span>
          </button>
        ))}
      </div>
    </div>
  );
} 